body {
  margin: 0;
  padding: 0;
  font-family: Arial, "Helvetica Neue", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", 
    BlinkMacSystemFont, "Segoe UI", -apple-system, 
    sans-serif;
  color: #edd;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	background-color: #d11;
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.header {
	display: flex;
	justify-content: space-between;
	margin: 10px 20px;
}

.rightButtons {
	display: flex;
	align-items: center;
}
.logoImage {
	width: 20em;
}

.logoContainer {
	display: flex;
	align-items: center;
	padding: .5em;
}

@media only screen and (max-width: 500px){
	.logoImage {
		max-width: 50vw;
	}

	.logoContainer {
		padding: 0;
	}
}
.headerButton {
	padding: 1em .5em;
	width: 3em;
	cursor: pointer;
}


@media only screen and (max-width: 500px){
 .headerButton {
 	font-size: .65em;
 }
}
.body {
	margin: 2em 0;
}
.timer {
	display: flex;
	justify-content: center;
	font-size: 5em;
  color: white;
	padding-bottom: .2em;
}
.PlayPauseBar {
	display: flex;
	justify-content: center;
}
.smallButton {
	width: 70px;
	height: 70px;
	margin: .5em;
	outline: none;
	-webkit-transition: all .2s;
	transition: all .2s;
	cursor: pointer;
}

.smallButton:hover {
	-webkit-animation: pound 3s infinite;
	        animation: pound 3s infinite;
	-webkit-animation-delay: 2s;
	        animation-delay: 2s;
}

.smallButton:active {
	-webkit-transform: translateY(-2px);
	        transform: translateY(-2px);
}

@-webkit-keyframes pound {
	0%, 50% {
    -webkit-transform: scale(1);
            transform: scale(1);
	}

  25% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  12.5%, 37.5% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}

@keyframes pound {
	0%, 50% {
    -webkit-transform: scale(1);
            transform: scale(1);
	}

  25% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  12.5%, 37.5% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}
.PomodoroBreakBar {
	display: flex;
	justify-content: center;
}
.bigButton {
	padding: .5em;
	cursor: pointer;
	-webkit-transition: all .3s;
	transition: all .3s;
	width: 120px;
	height: 120px;
}

.bigButton:hover {
	-webkit-transform: rotate(-10deg);
	        transform: rotate(-10deg);
}

.bigButton:active {
	-webkit-transform: scale(1.1) rotate(-10deg);
	        transform: scale(1.1) rotate(-10deg);
}

@media only screen and (max-width: 400px) {
	.bigButton {
		width: 90px;
		height: 90px;
		padding: .3em;
	}
}
.image {
	display: flex;
	justify-content: center;
	margin: 1em;
}

.settingsView {
	height: 300px;
	width: 250px;
	z-index: 1;
	background-color: #070;
	margin: auto auto;
	display: flex;
	flex-flow: column wrap;
	align-items: center;
	justify-content: space-around;
	padding: 1em;
	border: 1.5px solid #222;
	border-radius: 10%;
}

.settingsView p {
	margin-bottom: 8px;
	-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}
.acceptButton {
	padding: 1em;
	margin: 0 .5em;
	cursor: pointer;
	background-color: #8b0000;
	color: #fee;
	font-size: .9em;
  border: 1px solid #000;
	outline: none;
	-webkit-transition: all .2s;
	transition: all .2s;
	border-radius: 20px;
}

.acceptButton:hover {
	background-color: #ad0000;
}

.acceptButton:active {
	-webkit-transform: translateY(2px);
	        transform: translateY(2px);
}
.slider {
	display: block;
	margin: 10px;
}

.slider {
  -webkit-appearance: none;
  width: 150px;
  height: 10px;
  border-radius: 5px;   
  background: #ddd;
  outline: none;
  opacity: 0.9;
  -webkit-transition: .2s;
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%; 
  border: 1px solid #000;
  background: #c10000;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid #000;
  background: #c10000;
  cursor: pointer;
}

.label {
	margin: 10px;
	display: block;
	text-align: center;
}

.sliderContainer {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.modal {
	position: fixed;
	z-index: 1;
	left: -10%;
	top: -10%;
	width: 120%;
	height: 120%;
	background-color: rgba(0,0,0,.3);
}

.on {
	display: flex;
	justify-content: center;
}
.aboutView {
	z-index: 1;
	background-color: #070;
	margin: auto auto;
	width: 50vw;
	height: 80vh;
	display: flex;
	flex-flow: column wrap;
	align-items: center;
	justify-content: space-around;
	padding: 1em 3em;
	border: 1.5px solid black;
	border-radius: 10%;
}

article {	
	display: flex;
	padding: 1em 2em;
	flex-direction: column;
	overflow: auto;
	-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

article p {
	text-align: justify;
	margin: 0;
}

article h2 {
	font-size: 3vw;
	text-align: center;
}

@media only screen and (max-width: 600px){
	article {
		padding-left: 0;
	}

	article p {
		text-align: left;
	}

	article h2 {
		font-size: 5vw;
	}
}
